import React, { useEffect, useState } from 'react';
import objetosParaArray from '../../util/array';
import EquipeChamadorModel from '../../models/realtimedatabase/equipe';

import dbRealtimeDatabase from '../../configs/firebase';
import { get, onValue, ref } from 'firebase/database';

import logoOBR from '../../assets/images/logo_obr2025.png';
import logoCITEC from '../../assets/images/logo_citec2024.png';
import mascote from '../../assets/images/mascote2025.png';
import './styles.css';
import ChamadorProvider from '../../providers/realtimedatabase/chamador';

export default function TelaApresentacao(){

    const [equipeChamadaNivelUm, setEquipeChamadaNivelUm] = useState<EquipeChamadorModel>();
    const [equipeChamadaNivelDois, setEquipeChamadaNivelDois] = useState<EquipeChamadorModel>();
    const [proximasEquipesNivelUm, setProximasEquipesNivelUm] = useState<EquipeChamadorModel[]>([]);
    const [proximasEquipesNivelDois, setProximasEquipesNivelDois] = useState<EquipeChamadorModel[]>([]);
    const [acenderBackgroundUm, setAcenderBackgroundUm] = useState<boolean>(false);
    const [acenderBackgroundDois, setAcenderBackgroundDois] = useState<boolean>(false);
    const [equipes, setEquipes] = useState<EquipeChamadorModel[]>([]);

    const [quantidadeVisivelAguardando, setQuantidadeVisivelAguardando] = useState<number>(3);
    const [quantidadeVisivelProximos, setQuantidadeVisivelProximos] = useState<number>(3);

    /**
     * Busca as equipes cadastradas no banco de dados firestore
     */
     const buscarEquipes = async () => {
        try{
            let chamadorProvider = new ChamadorProvider();

            // L os dois nveis e salva tudo em um nico array
            let equipesNivel1: EquipeChamadorModel[] = await chamadorProvider.buscarEquipes("chamadas", "nivel_um");
            let equipesNivel2: EquipeChamadorModel[] = await chamadorProvider.buscarEquipes("chamadas", "nivel_dois");

            let equipes: EquipeChamadorModel[] = [...equipesNivel1, ...equipesNivel2];

            equipes = equipes.sort((a:EquipeChamadorModel, b:EquipeChamadorModel) => {
                const horarioA = a.horarioUltimaChamada!.split(':').map(Number);
                const horarioB = b.horarioUltimaChamada!.split(':').map(Number);
                
                for (let i = 0; i < horarioA.length; i++) {
                    if (horarioA[i] !== horarioB[i]) {
                    return horarioA[i] - horarioB[i];
                    }
                }  

                return 0;
            });

            equipes.reverse();  // Inverte o array para que seja mostrado as ltimas equipes chamadas no topo

            setEquipes(equipes.slice(0, quantidadeVisivelAguardando));
        }catch(erro){
            alert(erro);
        }
    }

    /**
     * Ao iniciar a pgina, adiciona listeners as referncias do banco de dados realtimedatabase para que quando um dado for alterado ele atualize a lista
     */
    useEffect(() => {
        buscarEquipes();
        let removerListenerListarUltimasChamadas = onValue(ref(dbRealtimeDatabase, "equipes/chamadas"), (snapshot) => {
            buscarEquipes();
        });

        // ltima equipe chamada - Nvel um 
        let removerListenerChamadasNivelUm = onValue(ref(dbRealtimeDatabase, "equipes/chamadas/nivel_um"), (snapshot) => {
            if(snapshot.exists() === true){
                let ultimasEquipes = objetosParaArray([snapshot.val()]).reverse();  //Usa reverse para que os ltimos fiquem no comeo do array
                setEquipeChamadaNivelUm(ultimasEquipes[0]);

                //Acende o background de equipes
                setAcenderBackgroundUm(true);
                //Apaga o background do container da equipe aps alguns segundos (funo assncrona)
                setTimeout(() => setAcenderBackgroundUm(false), 1200);
            }
        });

        // ltima equipe chamada - Nvel dois
        let removerListenerChamadasNivelDois = onValue(ref(dbRealtimeDatabase, "equipes/chamadas/nivel_dois"), (snapshot) => {
            if(snapshot.exists() === true){
                let ultimasEquipes = objetosParaArray([snapshot.val()]).reverse();
                setEquipeChamadaNivelDois(ultimasEquipes[0]);

                setAcenderBackgroundDois(true);
                setTimeout(() => setAcenderBackgroundDois(false), 1200);
            }
        });

        // Prximas equipes - Nvel um
        let removerListenerAguardandoNivelUm = onValue(ref(dbRealtimeDatabase, "equipes/aguardando/nivel_um"), (snapshot) => {
            if(snapshot.exists() === true){
                let resultadoConsulta = snapshot.val();
                setProximasEquipesNivelUm(prepararListaProximos(resultadoConsulta).slice(0, quantidadeVisivelAguardando));
                // console.log(prepararListaProximos(resultadoConsulta).slice(0, quantidadeVisivelAguardando))
            }
        });

        // Prximas equipes - Nvel dois
        let removerListenerAguardandoNivelDois = onValue(ref(dbRealtimeDatabase, "equipes/aguardando/nivel_dois"), (snapshot) => {
            if(snapshot.exists() === true){
                let resultadoConsulta = snapshot.val();
                setProximasEquipesNivelDois(prepararListaProximos(resultadoConsulta).slice(0, quantidadeVisivelAguardando));
            }
        });

        // Remove os listeners configurados
        return () => {
            removerListenerAguardandoNivelUm();
            removerListenerAguardandoNivelDois();
            removerListenerChamadasNivelUm();
            removerListenerChamadasNivelDois();
            removerListenerListarUltimasChamadas();
        };
    }, [])

    const prepararListaProximos = (resultadoConsulta: any) => {
        let proximasEquipes: EquipeChamadorModel[] = [];

        if(resultadoConsulta.arena_a !== undefined){
            proximasEquipes = objetosParaArray([resultadoConsulta.arena_a]);
        }

        if(resultadoConsulta.arena_b !== undefined){
            proximasEquipes = proximasEquipes.concat(objetosParaArray([resultadoConsulta.arena_b]));
        }

        if(resultadoConsulta.arena_c !== undefined){
            proximasEquipes = proximasEquipes.concat(objetosParaArray([resultadoConsulta.arena_c]));
        }

        return proximasEquipes;
    }

    return(
        <div id="tela-apresentacao">
            <header>
                <h1>Chamada das equipes</h1>
            </header>

            <main>
                <div id="equipes-chamadas">
                    <div  className={`container-equipe ${acenderBackgroundUm ? " animacao-piscar" : ""}`}>
                        {/* <img src={mascote} alt="Mascote da OBR" id={(acenderBackgroundUm || acenderBackgroundDois)?"mascote-animacao":"mascote"}/> */}
                        <img src={mascote} alt="Mascote da OBR" id={"mascote"}/>
                        <h2 className="texto-nivel">Nvel um</h2>
                        <h1>{equipeChamadaNivelUm !== undefined ? equipeChamadaNivelUm.name : "Aguardando"}</h1>
                        <h2 className="texto-nivel">Arena: {equipeChamadaNivelUm !== undefined ? equipeChamadaNivelUm.numero_arena : 0}</h2>
                    </div>

                    <div className={`container-equipe ${acenderBackgroundDois ? " animacao-piscar" : ""}`}>
                        <h2  className="texto-nivel">Nvel dois</h2>
                        <h1>{equipeChamadaNivelDois !== undefined ? equipeChamadaNivelDois.name : "Aguardando"}</h1>
                        <h2 className="texto-nivel">Arena: {equipeChamadaNivelDois !== undefined ? equipeChamadaNivelDois.numero_arena : 0}</h2>
                    </div>

                    <div id={`container-ultimas-chamadas`}>
                        <div className='linha-ultimas-chamadas'>
                            <h2  className="texto-nivel">Chamados recentemente</h2>
                        </div>
                        <table cellSpacing="0" cellPadding="0" width={"100%"}>
                            <tbody>
                                <tr>
                                    <td><p>Nmero</p></td>
                                    <td><p>Hora</p></td>
                                    <td><p>Nome</p></td>
                                    <td><p>Arena</p></td>
                                </tr>
                                {equipes.length > 0 
                                    ?
                                    <>
                                        {equipes.map((equipeChamada, index) => {
                                            return (
                                                <tr key={index}>
                                                    <td><p>{(index + 1)}</p></td>
                                                    <td><p>{equipeChamada.horarioUltimaChamada}</p></td>
                                                    <td><p>{equipeChamada.name}</p></td>
                                                    <td><p>{equipeChamada.numero_arena}</p></td>
                                                </tr>
                                            );
                                        })}
                                    </>
                                    :
                                    <p className="lista-vazia">Lista vazia</p>
                                }
                            </tbody>
                        </table>
                    </div>
                </div>
                
                <div id="proximas-equipes">
                    <div className="container-lista">
                        <h2 id="texto-nivel" className="texto-nivel">Prximos - Nvel um</h2>

                        {proximasEquipesNivelUm.length > 0 
                            ?
                                <table cellSpacing="0" cellPadding="0">
                                    <tbody>
                                        {proximasEquipesNivelUm.map((proximaEquipe, index) => {
                                            return(
                                                <tr key={index}>
                                                    <td><p>{(index + 1)}</p></td>
                                                    <td ><p>{proximaEquipe.name}</p></td>
                                                </tr>
                                            );
                                        })}
                                    </tbody>
                                </table>
                            :
                                <p className="lista-vazia">Lista vazia</p>
                            }
                    </div>

                    <div className="container-lista">
                        <h2 id="texto-nivel" className="texto-nivel">Prximos - Nvel dois</h2>

                        {proximasEquipesNivelDois.length > 0
                        ?
                            <table cellSpacing="0" cellPadding="0">
                                <tbody>
                                    {proximasEquipesNivelDois.map((proximaEquipe, index) => {
                                        return(
                                            <tr key={index}>
                                                <td><p>{(index + 1)}</p></td>
                                                <td><p>{proximaEquipe.name}</p></td>
                                            </tr>
                                        );
                                    })}
                                </tbody>
                            </table>
                        :
                            <p className="lista-vazia">Lista vazia</p>
                        }
                    </div>
                </div>
            </main>
            
            <footer id="logomarcas">
                <div>
                    <img src={logoOBR} alt="Logomarca da OBR"/>
                    <img src={logoCITEC} alt="Logomarca do CITEC"/>
                </div>
            </footer>
        </div>
    );

}