import axios from 'axios';
import IEventoModel from '../models/evento';
import EquipeChamadorModel from '../models/realtimedatabase/equipe';

export default class TournamenterProvider{
    
    protected api = axios.create({
        baseURL: 'https://olimpo.robocup.org.br/api/'
    });

    /**
     * Busca as informaes do evento cadastro no sistema olimpo, que tem o token especificado por parmetro
     * @param tokenOlimpo
     * @returns 
     */
     public async buscarEvento(tokenOlimpo: string): Promise<IEventoModel>{
        try{
            const resposta = await this.api.get('events/steps/participants?token='+tokenOlimpo);
            let evento: IEventoModel = resposta.data;
            evento.nivel = evento.name[3];
            return evento;
        }catch(erro){
            throw erro;
        }
    }

    /**
     * Busca as equipes cadastradas nos sistema olimpo para o evento com o token especificado
     * @param tokenOlimpo
     * @returns 
     */
     public async buscarEquipes(tokenOlimpo: string): Promise<EquipeChamadorModel[]>{
        try{
            const evento: IEventoModel = await this.buscarEvento(tokenOlimpo);

            // Adiciona o atributo nvel s equipes
            const equipes: EquipeChamadorModel[] = [];
            evento.teams.forEach((equipe) => {
                equipe.nivel = evento.nivel;
                equipes.push(equipe);
            });

            return equipes;
        }catch(erro){
            throw erro;
        }
    }

}