import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

import logoCITEC from '../../assets/images/logo_citec2024.png';
import logoOBR from '../../assets/images/logo_obr2025.png';
import mascote from '../../assets/images/mascote2025.png';
import ChamadorProvider from '../../providers/realtimedatabase/chamador';
import './styles.css';

export default function TelaLogin(){
    const navigate = useNavigate();
    const [senha, setSenha] = useState<string>("");

    /**
     * Autentica no banco de dados. Caso a autenticao seja bem sucedida, redireciona o usurio para a pgina principal do chamador.
     */
    const autenticar = async (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        try{
            //PreventDefault para no dar reload na pgina
            event.preventDefault();

            //Autenticao
            let chamadorProvider: ChamadorProvider = new ChamadorProvider();
            if(senha !== "" && await chamadorProvider.autenticar(senha) === true){
                navigate("/chamador");
            }else{
                alert("Senha incorreta");
            }
        }catch(erro){;
            alert(erro);
        }
    }

    return(
        <div id="tela-login">
            <img className="mascote" src={mascote} alt="Mascote da OBR" />
            <header>
                <h1>Autenticao</h1>
                <p>Insira a senha fornecida pelo administrador para poder ter acesso ao chamador.</p>
            </header>

            <form>
                <div className="form-group">
                    <label htmlFor="senha">Senha</label>
                    <input id="senha" type="password" onChange={(event) => setSenha(event.target.value)} />
                </div>

                <div id="botoes-formulario">
                    <button type="submit" onClick={(event) => autenticar(event)}>Entrar</button>
                </div>
            </form>

            <div id="logomarcas">
                <div>
                    <img src={logoCITEC} alt="Logomarca do CITEC" />
                    <img src={logoOBR} alt="Logomarca da OBR" />
                </div>
            </div>
        </div>
    )
}