import React, { useEffect } from 'react';
import { useState } from "react";
import { useNavigate } from 'react-router-dom';
import Loading from '../../components/loading';
import IEventoModel from '../../models/evento';
import ChamadorProvider from '../../providers/realtimedatabase/chamador';
import TournamenterProvider from '../../providers/tournamenter';

import './styles.css';

export default function TelaConfiguracoes(){
    const navigate = useNavigate();
    const [tokenOlimpo, setTokenOlimpo] = useState<string>("");
    const [equipesCadastradas, setEquipesCadastradas] = useState<boolean>(false);
    const [senha, setSenha] = useState<string>("");
    const [statusCarregando, setStatusCarregando] = useState<string>("");

    useEffect(() => {
        checarCadastros();
    }, []);

    /**
     * Checa se j existem dados cadastrados no sistema.
     * Se sim, libera o boto de apagar (se j tiver feito login), caso contrrio, libera os campos para cadastro.
     */
    const checarCadastros = async () => {
        try{
            setStatusCarregando("Buscando cadastros...");
            let chamadorProvider: ChamadorProvider = new ChamadorProvider();
            if(await chamadorProvider.possuiCadastros() > 0){
                setEquipesCadastradas(true);
            }else{
                setEquipesCadastradas(false);
            }
        }catch(erro){
            alert(erro);
        }finally{
            setStatusCarregando("");
        }
    }

    /**
     * Cadastra as equipes no banco de dados firebase
     */
    const cadastrarEquipes = async () => {
        try{
            if(senha !== "" && tokenOlimpo !== ""){
                let continuarCadastro = true;

                if(equipesCadastradas === true){
                    continuarCadastro = window.confirm("J existem equipes cadastradas no sistema. Deseja adicionar as equipes do token digitado? \nSe sim, aperte OK. Se no, aperte em Cancelar.");
                }
    
                if(continuarCadastro === true){
                    setStatusCarregando("Cadastrando equipes...");
                    let chamadorProvider: ChamadorProvider = new ChamadorProvider();
                    await chamadorProvider.cadastrarEquipes(senha, tokenOlimpo);
                    alert("Pronto, as equipes foram cadastradas. Voc est pronto para usar o chamador.");
                    navigate("/chamador");
                }
            }else{
                throw new Error("Preencha todos os campos");
            }
        }catch(erro){
            alert(erro);
        }finally{
            setStatusCarregando("")
        }
    }

    /**
     * Apaga todo o banco de dados firebase
     */
    const apagarEquipes = async () => {
        try{
            setStatusCarregando("Apagando todas as equipes...");
            let chamadorProvider: ChamadorProvider = new ChamadorProvider();
            await chamadorProvider.apagarEquipes(senha);
            await checarCadastros();
        }catch(erro){
            alert(erro);
        }finally{
            setStatusCarregando("");
        }
    }

    return(
        <div id="tela-configuracoes">
            <header>
                <h1>Gerenciamento de equipes</h1>
                <p>Adicione as equipes inscritas no sistema olimpo ao banco de dados firebase para poder comear a cham-las.</p>
            </header>

            <form>
                <div className="form-group">
                    <label htmlFor="token">Token do sistema olimpo</label>
                    <input type="password" id="token" onChange={(event) => setTokenOlimpo(event.target.value)} />
                </div>
                
                <div className="form-group">
                    <label htmlFor="senha">Senha do chamador</label>
                    <input id="senha" type="password" onChange={(event) => setSenha(event.target.value)}/>
                </div>
                
                <div id="botoes-formulario">
                    <button type="button" onClick={() => cadastrarEquipes()}>Adicionar equipes do evento</button>
                    {/* <button type="button" onClick={() => apagarEquipes()}>Exportar chamadas em um excel</button> */}
                    <button type="button" className="btn-perigo" onClick={() => apagarEquipes()} disabled={!equipesCadastradas}>Apagar equipes j cadastradas</button>
                </div>
            </form>

            <Loading statusLoading={statusCarregando} />
        </div>
    );
}