import TournamenterProvider from '../tournamenter';
import db from '../../configs/firebase';
import { getAuth, createUserWithEmailAndPassword, signInWithEmailAndPassword, deleteUser } from "firebase/auth";
import { child, get, limitToFirst, query, ref, remove, set } from 'firebase/database';
import objetosParaArray from '../../util/array';

import EquipeChamadorModel from '../../models/realtimedatabase/equipe';

export default class ChamadorProvider{

    private emailAutenticacao: string = "roboticacesmac@gmail.com";

    /**
     * Retorna true se houver cadastros realizados no sistema
     */
    public async possuiCadastros(): Promise<number> {
        try{
            let resultado = await get(ref(db, "equipes"));
            return resultado.size;
        }catch(erro){
            console.error("Erro no mtodo possuiCadastros() do provider chamador: "+erro);
            throw erro;
        }
    }

    /**
     * Autentica o usurio no banco de dados firebase.
     * Retorna true se a autenticao for realizada com sucesso.
     * @param senhaChamador 
     * @returns 
     */
    public async autenticar(senhaChamador: string): Promise<boolean> {
        try{
            let credencialUsuario = await signInWithEmailAndPassword(getAuth(), this.emailAutenticacao, senhaChamador);
            return (credencialUsuario.user !== null);
        }catch(erro){
            console.error("Erro no mtodo autenticar() do provider chamador: "+erro);
            throw erro;
        }
    }

    /**
     * Cria uma autenticao e cadastra as equipes no banco de dados Realtime Database, para que possam ser chamadas.
     * @param eventoID 
     */
    public async cadastrarEquipes(senhaChamador: string, tokenOlimpo: string): Promise<void> {
        try{
            //Cria a autenticao do usurio ou se autentica se ela j estiver criada
            await createUserWithEmailAndPassword(getAuth(), this.emailAutenticacao, senhaChamador).catch(async (erro) => {
                if(erro.code === "auth/email-already-in-use"){
                    await this.autenticar(senhaChamador);
                }
            });

            //Busca as equipes no sistema olimpo
            let tournamenterProvider: TournamenterProvider = new TournamenterProvider();
            let equipes: EquipeChamadorModel[] = await tournamenterProvider.buscarEquipes(tokenOlimpo);

            //Cadastra as equipes no firebase
            if(equipes.length > 0){
                equipes.forEach(async (equipe) => {
                    let referenciaBanco = 'equipes/aguardando';
                    if(equipe.nivel === "Nvel 1" || equipe.nivel === "Nvel 1"){
                        referenciaBanco += "/nivel_um";
                    }else if (equipe.nivel === "Nvel 2" || equipe.nivel === "Nvel 2"){
                        referenciaBanco += "/nivel_dois";
                    }

                    equipe.tipo_arena = "a";
                    await set(ref(db, referenciaBanco+"/arena_a/"+equipe.id), equipe);
                    equipe.tipo_arena = "b";
                    await set(ref(db, referenciaBanco+"/arena_b/"+equipe.id), equipe);
                    equipe.tipo_arena = "c";
                    await set(ref(db, referenciaBanco+"/arena_c/"+equipe.id), equipe);
                });
            }
        }catch(erro){
            console.error("Erro no mtodo cadastrarEquipes do provider chamador: "+erro);
            throw erro;
        }
    }

    /**
     * Apaga todas as equipes gravadas no banco de dados Realtime Database e a autenticao do chamador
     */
    public async apagarEquipes(senhaChamador: string): Promise<void> {
        try{
            let credencialUsuario = await signInWithEmailAndPassword(getAuth(), this.emailAutenticacao, senhaChamador);
            if(credencialUsuario.user !== null){
                await remove(ref(db, "equipes"));
                await deleteUser(credencialUsuario.user);
            }else{
                throw Error("Senha incorreta");
            }
        }catch(erro){
            console.error("Erro no mtodo apagarEquipes do provider chamador: "+erro);
            throw erro;
        }
    }

    /**
     * Chama a equipe de determinada modalidade e para determinado desenho de arena (A, B ou C)
     *  chamado o primeiro encontrado na busca que  do nvel correspondente e ainda no participou da arena informada
     * @param nivel
     * @param tipoArena 
     * @param numeroArena 
     * @returns Retorna a equipe que foi chamada
     */
    public async chamarEquipe(nivel: "nivel_um" | "nivel_dois" | string,  tipoArena: "a" | "b" | "c" | string, numeroArena: number): Promise<EquipeChamadorModel> {
        try{
            let consulta = query(ref(db, "equipes/aguardando/"+nivel+"/arena_"+tipoArena), limitToFirst(1));
            let snapshotEquipe = await get(consulta);

            if(snapshotEquipe.exists() === true){
                let resultadoConsulta = snapshotEquipe.val();
                let equipe: EquipeChamadorModel;
                equipe = resultadoConsulta[Object.keys(resultadoConsulta)[0]];  // Primeiro objeto retornado (Object.Keys para pegar as propriedades dos objetos retornados, que no caso so os IDs de cada objeto retornado da consulta)

                let horarioAgora = new Date().toLocaleTimeString();

                equipe.numero_arena = numeroArena;
                equipe.horarioUltimaChamada = horarioAgora;

                // Coloca a equipe na fila para ser falada no som
                await set(ref(db, "equipes/chamadas/fila_som/"+equipe.id), equipe);

                // Guarda nas equipes chamadas
                await set(ref(db, "equipes/chamadas/"+nivel+"/"+horarioAgora), equipe);

                // Deleta das equipes aguardando
                await remove(ref(db, "equipes/aguardando/"+nivel+"/arena_"+tipoArena+"/"+equipe.id));

                return equipe;
            }else{
                throw new Error("Nenhuma equipe encontrada com as condies informadas. Provalvelmente todas as equipes j participaram da arena informada.");
            }
        }catch(erro){
            console.error("Erro no mtodo chamarEquipe do provider chamador: "+erro);
            throw erro;
        }
    }

    /**
     * Chama a equipe que tem o ID determinado
     * @param idEquipe 
     * @returns
     */
    public async chamarEquipePorID(idEquipe: string, nivel: "nivel_um" | "nivel_dois" | string,  tipoArena: "a" | "b" | "c" | string, numeroArena: number): Promise<void>{
        try{
            let snapshotEquipe = await get(ref(db, "equipes/aguardando/"+nivel+"/arena_"+tipoArena+"/"+idEquipe));

            if(snapshotEquipe.exists()){
                let equipe: EquipeChamadorModel = snapshotEquipe.val();                
                equipe.numero_arena = numeroArena;

                let horarioAgora = new Date().toLocaleTimeString();

                equipe.numero_arena = numeroArena;
                equipe.horarioUltimaChamada = horarioAgora;

                // Coloca a equipe na fila para ser falada no som
                await set(ref(db, "equipes/chamadas/fila_som/"+equipe.id), equipe);

                // Guarda nas equipes chamadas
                await set(ref(db, "equipes/chamadas/"+nivel+"/"+horarioAgora), equipe);

                // Deleta das equipes aguardando
                await remove(ref(db, "equipes/aguardando/"+nivel+"/arena_"+tipoArena+"/"+equipe.id));
            }else{
                throw new Error("Essa equipe no foi encontrada na lista de equipes");
            }
        }catch(erro){
            console.error("Erro no mtodo chamarEquipePorID do provider chamador: "+erro);
            throw erro;
        }
    }

    /**
     * Chama a equipe passada por parmetro novamente
     * @param equipe 
     */
    public async chamarEquipeNovamente(equipe: EquipeChamadorModel): Promise<void> {
        try{
            if(equipe !== undefined){
                let nomeEquipe = equipe.name;
                let nivel: string = nomeEquipe.split(" -")[0];
                if(nivel === "1"){
                    nivel = "nivel_um";
                }else{
                    nivel = "nivel_dois";
                }

                let horarioAgora = new Date().toLocaleTimeString();
                equipe.horarioUltimaChamada = horarioAgora;

                // Coloca a equipe na fila para ser falada no som
                await set(ref(db, "equipes/chamadas/fila_som/"+equipe.id), equipe);

                // Guarda nas equipes chamadas
                await set(ref(db, "equipes/chamadas/"+nivel+"/"+horarioAgora), equipe);
            }else{
                throw new Error("Equipe no informada");
            }
        }catch(erro){
            console.error("Erro no mtodo chamarEquipeNovamente do provider chamador: "+erro);
            throw erro;
        }
    }

    /**
     * Busca as equipes de acordo com as condies passadas por parmetro e retorna em um array
     * @param estadoEquipe
     * @param nivel
     * @returns
     */
     public async buscarEquipes(estadoEquipe: "inativas" | "aguardando" | "chamadas" | string, nivel: "nivel_um" | "nivel_dois" | string):  Promise<EquipeChamadorModel[]>{
        try{
            let listaEquipes: EquipeChamadorModel[] = [];

            if(estadoEquipe === "aguardando" || estadoEquipe == "inativas"){
                let snapshotArenaA = await get(ref(db, "equipes/"+estadoEquipe+"/"+nivel+"/arena_a"));
                let snapshotArenaB = await get(ref(db, "equipes/"+estadoEquipe+"/"+nivel+"/arena_b"));
                let snapshotArenaC = await get(ref(db, "equipes/"+estadoEquipe+"/"+nivel+"/arena_c"));
    
                listaEquipes = objetosParaArray([snapshotArenaA.val(), snapshotArenaB.val(), snapshotArenaC.val()]);
            }else{
                let snapshot = await get(ref(db, "equipes/"+estadoEquipe+"/"+nivel));
                listaEquipes = objetosParaArray([snapshot.val()]);
            }

            return listaEquipes;
        }catch(erro){
            console.error("Erro no mtodo buscarEquipesAguardando() do provider chamador: "+erro);
            throw erro;
        }
    }

    /**
     * Busca as ltimas equipes chamadas do nvel passado por parmetro
     * @param nivel
     * @returns
     */
    public async buscarUltimasEquipesChamadas(nivel: "nivel_um" | "nivel_dois" | string): Promise<EquipeChamadorModel[]>{
        try{
            let limiteResultados = 5;
            let snapshotEquipes = await get(query(ref(db, "equipes/chamadas/"+nivel), limitToFirst(limiteResultados)));

            let equipes: EquipeChamadorModel[] = [];
            if(snapshotEquipes.exists()){
                let resultadoConsulta = snapshotEquipes.val();

                // Adiciona cada objeto que est dentro do objeto resultadoConsulta, dentro do array de equipes. 
                equipes = objetosParaArray([resultadoConsulta]);
            }
            
            return equipes;
        }catch(erro){
            console.error("Erro no mtodo buscarUltimasEquipesChamadas do provider chamador: "+erro);
            throw erro;
        }
    }

    /**
     *  Consulta em ordem crescente o banco de dados procurando pelas 5 primeiras
     *  equipes com arena A no jogada. Se no encontrar, procurar por arena B, se no encontrar,
     *  procurar por arena C. Dessa forma, sabe quais so as 5 prximas que iro ser chamadas, considerando que
     *  primeiro so jogadas as arenas_a para depois as arenas_b e por ltimo as arenas_c.
     */
    public async buscarProximasEquipes(nivel: "nivel_um" | "nivel_dois" | string): Promise<EquipeChamadorModel[]>{
        try{
            let limiteResultados = 5;

            // Busca as limiteResultados primeiras equipes aguardando serem chamadas para a arena_a
            let snapshotEquipes = await get(query(ref(db, "equipes/aguardando/"+nivel+"/arena_a"), limitToFirst(limiteResultados)));
            
            let ultimasEquipesChamadas: EquipeChamadorModel[] = [];
            if(snapshotEquipes.exists() === true){
                ultimasEquipesChamadas = objetosParaArray([snapshotEquipes.val()]);
            }

            // Se ainda no tiver alcanado limiteResultados equipes, ento junta as da arena_a com as da arena_b com a quantidade que falta para completar o limiteResultados
            let novoLimiteConsulta = limiteResultados - ultimasEquipesChamadas.length;
            if(novoLimiteConsulta > 0){
                snapshotEquipes = await get(query(ref(db, "equipes/aguardando/"+nivel+"/arena_b"), limitToFirst(novoLimiteConsulta)));

                if(snapshotEquipes.exists() === true){
                    ultimasEquipesChamadas = ultimasEquipesChamadas.concat(objetosParaArray([snapshotEquipes.val()]));
                }
            }
    
            // Se ainda no tiver alcanado limiteResultados equipes, ento junta as da arena_a com as da arena_b com as da arena_c com a quantidade que falta para completar o limiteResultados
            novoLimiteConsulta = limiteResultados - ultimasEquipesChamadas.length;
            if(novoLimiteConsulta > 0){
                snapshotEquipes = await get(query(ref(db, "equipes/aguardando/"+nivel+"/arena_c"), limitToFirst(novoLimiteConsulta)));
                
                if(snapshotEquipes.exists() === true){
                    ultimasEquipesChamadas = ultimasEquipesChamadas.concat(objetosParaArray([snapshotEquipes.val()]));
                }
            }
    
            return ultimasEquipesChamadas;
        }catch(erro){
            console.error("Erro no mtodo buscarProximasEquipes do provider chamador: "+erro);
            throw erro;
        }
    }

    /**
     * Adiciona a equipe passada por parmetro  fila
     * @param equipe
     */
    public async adicionarEquipe(equipe: EquipeChamadorModel): Promise<void> {
        try{
            let nivel = "nivel_um";
            let arena = "arena_"+equipe.tipo_arena.toLowerCase();

            if(equipe.nivel === "Nvel 2" || equipe.nivel === "Nvel 2"){
                nivel = "nivel_dois";
            }

            await set(ref(db, "equipes/aguardando/"+nivel+"/"+arena+"/"+equipe.id), equipe);
        }catch(erro){
            console.error("Erro no mtodo adicionarEquipe do provider chamador: "+erro);
            throw erro;
        }
    }

    /**
     * Inativa a equipe na arena informada por parmetro para que ela no possa ser chamada
     * @param equipe 
     */
    public async inativarEquipe(equipe: EquipeChamadorModel): Promise<void> {
        try{
            let nivel = "nivel_um";
            let arena = "arena_"+equipe.tipo_arena.toLowerCase();

            if(equipe.nivel === "Nvel 2" || equipe.nivel === "Nvel 2"){
                nivel = "nivel_dois";
            }

            await remove(ref(db, "equipes/aguardando/"+nivel+"/"+arena+"/"+equipe.id));
            await set(ref(db, "equipes/inativas/"+nivel+"/"+arena+"/"+equipe.id), equipe);
        }catch(erro){
            console.error("Erro no mtodo inativarEquipe do provider chamador: "+erro);
            throw erro;
        }
    }

    /**
     * Reativa a equipe passada por parmetro para poder voltar a ser chamada
     * @param equipe 
     */
    public async reativarEquipe(equipe: EquipeChamadorModel): Promise<void> {
        try{
            let nivel = "nivel_um";
            let arena = "arena_"+equipe.tipo_arena.toLowerCase();

            if(equipe.nivel === "Nvel 2" || equipe.nivel === "Nvel 2"){
                nivel = "nivel_dois";
            }

            await remove(ref(db, "equipes/inativas/"+nivel+"/"+arena+"/"+equipe.id));
            await set(ref(db, "equipes/aguardando/"+nivel+"/"+arena+"/"+equipe.id), equipe);
        }catch(erro){
            console.error("Erro no mtodo reativarEquipe do provider chamador: "+erro);
            throw erro;
        }
    }
}