/**
 * Pega cada propriedade dos objetos passados por parmetro e agrupa todos eles em um s array.
 * Por exemplo: [{idUm: {a: 5, b: 2, c: 3}, idDois: {a: 2, b: 5, c: 3}}, {idUm: {a: 2, b: 3, c: 4}}] retorna [{a: 5, b: 2, c: 3}, {a: 2, b: 5, c: 3}, {a: 2, b: 3, c: 4}];
 *  til para uso com realtimedatabase que retorna objetos dentro de objetos
 * @param objetos 
 * @returns 
 */
export default function objetosParaArray(objetos: any[]): any[] {
    // Se tiver sido colocado um objeto ao invs de um array de objetos
    if(Array.isArray(objetos) === false){
        objetos = [objetos];
    }

    let arrayPropriedadesObjetos: any[] = [];

    objetos.forEach((objeto) => {
        if(objeto !== null){
            let arrayObjeto = Object.keys(objeto).map((keyPropriedade) => {
                return objeto[keyPropriedade];
            });
            
            arrayPropriedadesObjetos = arrayPropriedadesObjetos.concat(arrayObjeto);
        }
    });
    
    return arrayPropriedadesObjetos;
}