import { getAuth } from 'firebase/auth';
import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';

import Loading from '../loading';

interface IRotaPrivada {
    children: JSX.Element
}

export default function RotaPrivada(props: IRotaPrivada){
    const navigate = useNavigate();
    const [statusAutenticacao, setStatusAutenticacao] = useState<string>("Carregando...");

    /**
     * Quando o componente for carregado  checada a autenticao do usurio, para que 
     * s mostre a pgina privada se o usurio estiver autenticado.
     */
    useEffect(() => {
        setStatusAutenticacao("Checando autenticao...");
        getAuth().onAuthStateChanged(user => {
            if(user !== null){
                setStatusAutenticacao("");
            }else{
                navigate("/");
            }
        });
    }, []);

    return(
        <>
            {statusAutenticacao === "" 
            ?
                <>
                    {props.children}
                </>
            :
                <Loading statusLoading={statusAutenticacao} />
            }
        </>
    );
}