import React, { useEffect, useState } from 'react';
import EquipeChamadorModel from '../../models/realtimedatabase/equipe';
import ChamadorProvider from '../../providers/realtimedatabase/chamador';

import Modal from '../../components/modal';
import Loading from '../../components/loading';

import iconeAdicionar from '../../assets/images/icons/add.png'
import iconeMegafone from '../../assets/images/icons/megaphone.png'
import iconeInativar from '../../assets/images/icons/invisible.png'
import iconeAtivar from '../../assets/images/icons/eye.png'
import './styles.css';


export default function TelaEquipes(){

    const [equipes, setEquipes] = useState<EquipeChamadorModel[]>([]);
    const [estadoEquipe, setEstadoEquipe] = useState<string>("aguardando");
    const [nivel, setNivel] = useState<string>("nivel_um");
    const [statusCarregando, setStatusCarregando] = useState<string>("");

    const [modalChamadaVisivel, setModalChamadaVisivel] = useState<boolean>(false);
    const [equipeParaChamar, setEquipeParaChamar] = useState<EquipeChamadorModel>();
    const [numeroArena, setNumeroArena] = useState<number>(0);

    useEffect(() => {
        // buscarEquipes();
    }, [])

    /**
     * Busca as equipes cadastradas no banco de dados firestore
     */
    const buscarEquipes = async () => {
        try{
            setStatusCarregando("Buscando equipes...");
            let chamadorProvider = new ChamadorProvider();
            let equipes: EquipeChamadorModel[] = await chamadorProvider.buscarEquipes(estadoEquipe, nivel);

            if(estadoEquipe === "chamadas"){
                equipes.reverse();  // Inverte o array para que seja mostrado as ltimas equipes chamadas no topo
            }

            setEquipes(equipes);
        }catch(erro){
            alert(erro);
        }finally{
            setStatusCarregando("");
        }
    }

    /**
     * Abre o modal para chamar a equipe
     * @param indexArray 
     */
    const abrirModalChamadaEquipe = async (indexArray: number) => {
        try{
            if(equipes[indexArray] !== undefined){
                setModalChamadaVisivel(true);
                setEquipeParaChamar(equipes[indexArray]);
            }
        }catch(erro){
            alert(erro);
        }
    }

    /**
     * Chama a equipe para a arena de nmero informado
     * @param indexArray 
     */
    const chamarEquipe = async () => {
        try{
            setStatusCarregando("Chamando equipe...");
            
            let confirmacao = window.confirm("Confirma a chamada da equipe "+equipeParaChamar?.name+" para a arena de nmero "+numeroArena+" do tipo "+equipeParaChamar?.tipo_arena.toUpperCase()+"?");
            if(confirmacao === true){
                let chamadorProvider = new ChamadorProvider();
                chamadorProvider.chamarEquipePorID(equipeParaChamar!.id, nivel, equipeParaChamar!.tipo_arena, numeroArena);
                alert("Ok, a equipe "+equipeParaChamar?.name+" ser chamada.");
            }
        }catch(erro){
            alert(erro);
        }finally{
            setStatusCarregando("");
        }
    }

    /**
     * Adiciona uma arena a mais. Exemplo, se tem apenas B,C ento adiciona a arena A.
     * @param indexArray 
     */
     const adicionarEquipeFila = async (indexArray: number) => {
        try{
            let equipe = equipes[indexArray];
            let confirmacao = window.confirm("Tem certeza que deseja adicionar a equipe "+equipe.name+" novamente  fila da arena "+equipe.tipo_arena.toUpperCase()+"?");
            
            if(confirmacao === true){
                setStatusCarregando("Adicionando equipe...");
                let chamadorProvider = new ChamadorProvider();
                await chamadorProvider.adicionarEquipe(equipes[indexArray]);
                alert("Equipe "+equipe.name+" adicionada de volta  fila.");
            }
        }catch(erro){
            alert(erro);
        }finally{
            setStatusCarregando("");
        }
    }

    /**
     * Esconde a equipe da lista de equipes
     * @param indexArray 
     */
    const inativarEquipe = async (indexArray: number) => {
        try{
            let equipe = equipes[indexArray];
            let confirmacao = window.confirm("Tem certeza que deseja remover a equipe "+equipe.name+" da fila para a arena "+equipe.tipo_arena.toUpperCase()+"?");
            
            if(confirmacao === true){
                setStatusCarregando("Removendo equipe...");
                let chamadorProvider = new ChamadorProvider();
                await chamadorProvider.inativarEquipe(equipes[indexArray]);
                alert("Equipe "+equipe.name+" removida da fila da arena "+equipe.tipo_arena.toUpperCase()+". \n Voc pode adicion-la novamente listando pelas equipes inativas.");
                equipes.splice(indexArray, 1);
            }
        }catch(erro){
            alert(erro);
        }finally{
            setStatusCarregando("");
        }
    }

    /**
     * Exibe a equipe novamente na lista de equipes
     * @param indexArray 
     */
    const ativarEquipe = async (indexArray: number) => {
        try{
            let equipe = equipes[indexArray];
            let confirmacao = window.confirm("Tem certeza que deseja reativar a equipe "+equipe.name+" para a fila da arena "+equipe.tipo_arena.toUpperCase()+"?");
            
            if(confirmacao === true){
                setStatusCarregando("Reativando equipe...");
                let chamadorProvider = new ChamadorProvider();
                await chamadorProvider.reativarEquipe(equipes[indexArray]);
                alert("Equipe "+equipe.name+" foi colocada de volta na fila da arena "+equipe.tipo_arena.toUpperCase());
                equipes.splice(indexArray, 1);
            }
        }catch(erro){
            alert(erro);
        }finally{
            setStatusCarregando("");
        }
    }

    return(
        <div id="tela-equipes">
            <header>
                <h1>Equipes</h1>
                <p>Visualize a lista de equipes em ordem de chamada. Voc tambm pode chamar uma equipe ou adicion-la novamente  fila de aguardando. Voc tambm pode inativar equipes.</p>
            </header>
            
            <div className='content'>
                <form id="filtro">
                    <select value={estadoEquipe} onChange={(event) => {setEstadoEquipe(event.target.value); setEquipes([])}}>
                        <option value="aguardando">Aguardando</option>
                        <option value="chamadas">Chamadas</option>
                        <option value="inativas">Inativas</option>
                    </select>

                    <select value={nivel} onChange={(event) => {setNivel(event.target.value); setEquipes([])}}>
                        <option value="nivel_um">Nvel um</option>
                        <option value="nivel_dois">Nvel dois</option>
                    </select>

                    <button type="button" onClick={() => buscarEquipes()}>Carregar lista</button>
                </form>

                {equipes.length > 0
                ?
                    <table cellSpacing="0" cellPadding="0">
                        <thead>
                            <tr>
                                {(estadoEquipe === "aguardando" || estadoEquipe === "inativas") ? <th><p>N</p></th> : <th><p>Horas</p></th>}
                                <th><p>Nome</p></th>
                                {estadoEquipe === "chamadas" && <th><p>Arena</p></th>}
                                <th><p>Tipo</p></th>
                                <th><p>Aes</p></th>
                            </tr>
                        </thead>

                        <tbody>
                            {equipes.map((equipe, index) => {
                                return(
                                    <tr key={index}>
                                        {estadoEquipe === "aguardando" ? <td><p>{(index + 1)}</p></td> : <td><p>{equipe.horarioUltimaChamada}</p></td>}
                                        <td><p>{equipe.name}</p></td>
                                        {estadoEquipe === "chamadas" && <td><p>{equipe.numero_arena}</p></td>}
                                        <td><p>{equipe.tipo_arena.toUpperCase()}</p></td>
                                        <td>
                                            {estadoEquipe === "aguardando" &&
                                            <>
                                                <button type="button" title='Chamar equipe' onClick={() => abrirModalChamadaEquipe(index)}><img src={iconeMegafone} alt="Chamar equipe" /></button>
                                                <button className="btn-perigo" type="button" title='Inativar equipe' onClick={() => inativarEquipe(index)}><img src={iconeInativar} alt="Inativar equipe" /></button>
                                            </>
                                            }
                                            
                                            {estadoEquipe === "inativas" &&
                                                <button type="button" title='Ativar equipe' onClick={() => ativarEquipe(index)}><img src={iconeAtivar} alt="Inativar equipe" /></button>
                                            }

                                            {estadoEquipe === "chamadas" && 
                                                <button type="button" title='Adicionar arena' onClick={() => adicionarEquipeFila(index)}><img src={iconeAdicionar} alt="Adicionar arena" /></button>
                                            }
                                        </td>
                                    </tr>
                                );
                            })}
                        </tbody>
                    </table>
                :
                    <p id="lista-vazia">Lista vazia</p>
                }
            </div>

            <Modal titulo="Chamada" visivel={modalChamadaVisivel} onClose={() => setModalChamadaVisivel(false)}>
                <form id="modal-chamada">
                    {equipeParaChamar !== undefined && 
                        <p>Chamar equipe: {equipeParaChamar.name}</p>                
                    }
                    
                    <div className="form-group">
                        <label htmlFor="numero-arena">Nmero da arena</label>
                        <select id="numero-arena" value={numeroArena} onChange={(event) => setNumeroArena(parseInt(event.target.value))}>
                            <option disabled value={"0"}>Selecione</option>
                            <option value={"1"}>1</option>
                            <option value={"2"}>2</option>
                            <option value={"3"}>3</option>
                            <option value={"4"}>4</option>
                            <option value={"5"}>5</option>
                            <option value={"6"}>6</option>
                            <option value={"7"}>7</option>
                            <option value={"8"}>8</option>
                        </select>
                    </div>

                    <div id="botoes-formulario">
                        <button type="button" onClick={() => chamarEquipe()}>Chamar equipe</button>
                    </div>
                </form>
            </Modal>
            <Loading statusLoading={statusCarregando} />
        </div>
    );
}