import { Routes, Route, RouteProps, Navigate } from 'react-router-dom';

import TelaLogin from './pages/tela_login';
import TelaChamador from './pages/tela_chamador';
import TelaConfiguracoes from './pages/tela_configuracoes';
import TelaApresentacao from './pages/tela_apresentacao';
import TelaEquipes from './pages/tela_equipes';
import { getAuth } from 'firebase/auth';
import RotaPrivada from './components/autenticacao';

/**
 * Controlando autenticao em rotas no ReactJS | Diego Fernandes: https://youtu.be/sYe4r8WXGQg
 * @returns 
 */
 const Rotas = () => (
    <Routes>
        <Route path="/chamador" element={<RotaPrivada><TelaChamador /></RotaPrivada>} />
        <Route path="/configuracoes" element={<TelaConfiguracoes />} />
        <Route path="/apresentacao" element={<TelaApresentacao />} />
        <Route path="/equipes" element={<RotaPrivada><TelaEquipes /></RotaPrivada>} />
        <Route path="/*" element={<TelaLogin />} />
    </Routes>
);

export default Rotas;