import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { getAuth, signOut } from 'firebase/auth';
import ChamadorProvider from '../../providers/realtimedatabase/chamador';
import EquipeModel from '../../models/realtimedatabase/equipe';

import logoCITEC from '../../assets/images/logo_citec2024.png';
import logoOBR from '../../assets/images/logo_obr2025.png';
import './styles.css';

export default function TelaChamador(){
    const navigate = useNavigate();
    const [nivel, setNivel] = useState<string>("");
    const [tipoArena, setTipoArena] = useState<string>("");
    const [numeroArena, setNumeroArena] = useState<number>(0);
    const [ultimaEquipeChamada, setUltimaEquipeChamada] = useState<EquipeModel>();
    

    const [segundosEntreChamadaProximaEquipe, setSegundosEntreChamadaProximaEquipe] = useState<number>(600);
    const [segundosEntreChamadasMesmaEquipe, setSegundosEntreChamadasMesmaEquipe] = useState<number>(150);
    const [chamadaDisponivelProximaEquipe, setChamadaDisponivelProximaEquipe] = useState<boolean>(true);
    const [chamadaDisponivelMesmaEquipe, setChamadaDisponivelMesmaEquipe] = useState<boolean>(true);

    /**
     * Chama a prxima equipe que est disponvel para o nvel e tipo de arena informado.
     */
    const chamarProximaEquipe = async () => {
        try{
            if(nivel === "" || tipoArena === "" || numeroArena === 0){
                throw new Error("Preencha os campos!");
            }

            let confirmacao = window.confirm("Tem certeza que deseja chamar a equipe?");
            if(confirmacao === true){
                let chamadorProvider: ChamadorProvider = new ChamadorProvider();
                let equipeChamada = await chamadorProvider.chamarEquipe(nivel, tipoArena, numeroArena);
                setUltimaEquipeChamada(equipeChamada);
                alert("A equipe"+equipeChamada.name+" ser chamada.");
                
                //Desativa a chamada
                setChamadaDisponivelProximaEquipe(false);
                
                //Ativa a chamada aps alguns segundos (funo assncrona)
                setTimeout(() => setChamadaDisponivelProximaEquipe(true), segundosEntreChamadaProximaEquipe*1000);
            }
        }catch(erro){
            alert(erro);
        }
    }

    /**
     * Chama novamente a ltima equipe chamada atravs dessa pgina
     */
    const chamarNovamenteUltimaEquipe = async () => {
        try{
            if(ultimaEquipeChamada !== undefined){
                let confirmacao = window.confirm("Tem certeza que deseja chamar a equipe "+ultimaEquipeChamada.name+" novamente?");
                if(confirmacao === true){
                    let chamadorProvider: ChamadorProvider = new ChamadorProvider();
                    await chamadorProvider.chamarEquipeNovamente(ultimaEquipeChamada);
                    alert("A equipe"+ultimaEquipeChamada.name+" ser chamada.");

                    //Desativa a chamada
                    setChamadaDisponivelMesmaEquipe(false);
                    
                    //Ativa a chamada aps alguns segundos (funo assncrona)
                    setTimeout(() => setChamadaDisponivelMesmaEquipe(true), segundosEntreChamadasMesmaEquipe*1000);
                }
            }else{
                throw new Error("Nenhum registro de equipe chamada");
            }
        }catch(erro){
            alert(erro);
        }
    }

    /**
     * Desloga do banco de dados
     */
    const deslogar = async () => {
        try{
            let autenticacao = getAuth();
            await signOut(autenticacao);
            navigate("/");
        }catch(erro){
            alert(erro);
        }
    }

    return(
        <div id="tela-chamador">
            <header>
                <h1>Chamador de equipes</h1>
                <p>Selecione os campos abaixo de acordo com a sua arena. Voc precisa aguardar pelo menos 10 minutos a equipe chegar.</p>
            </header>

            <form>
                {ultimaEquipeChamada !== undefined && 
                    <p>ltima equipe chamada: {ultimaEquipeChamada.name}</p>                
                }

                <div className="form-group">
                    <label htmlFor="nivel">Nvel</label>
                    <select id="nivel" value={nivel} onChange={(event) => setNivel(event.target.value)}>
                        <option disabled value={""}>Selecione</option>
                        <option value={"nivel_um"}>Nvel 1</option>
                        <option value={"nivel_dois"}>Nvel 2</option>
                    </select>
                </div>

                <div className="form-group">
                    <label htmlFor="tipo-arena">Tipo da arena</label>
                    <select id="tipo-arena" value={tipoArena} onChange={(event) => setTipoArena(event.target.value)}>
                        <option disabled value={""}>Selecione</option>
                        <option value={"a"}>Arena A</option>
                        <option value={"b"}>Arena B</option>
                        <option value={"c"}>Arena C</option>
                    </select>
                </div>
                
                <div className="form-group">
                    <label htmlFor="numero-arena">Nmero da arena</label>
                    <select id="numero-arena" value={numeroArena} onChange={(event) => setNumeroArena(parseInt(event.target.value))}>
                        <option disabled value={"0"}>Selecione</option>
                        <option value={"1"}>1</option>
                        <option value={"2"}>2</option>
                        <option value={"3"}>3</option>
                        <option value={"4"}>4</option>
                        <option value={"5"}>5</option>
                        <option value={"6"}>6</option>
                        <option value={"7"}>7</option>
                        <option value={"8"}>8</option>
                    </select>
                </div>

                <div id="botoes-formulario">
                    <button type="button" disabled={chamadaDisponivelProximaEquipe === false} onClick={() => chamarProximaEquipe()}>Chamar prxima equipe</button>
                    <button type="button" disabled={(ultimaEquipeChamada === undefined || (chamadaDisponivelMesmaEquipe === false))} onClick={() => chamarNovamenteUltimaEquipe()}>Chamar novamente a equipe anterior</button>
                    <button type="button" onClick={() => deslogar()} className="btn-perigo">Desconectar</button>
                </div>
            </form>
            
            <div id="logomarcas">
                <div>
                    <img src={logoCITEC} alt="Logomarca do CITEC" />
                    <img src={logoOBR} alt="Logomarca da OBR" />
                </div>
            </div>
        </div>
    );

}